#HerderPart-outer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1000;
    background-color: white;
}

.HerderPart-inner {
    display: grid;
    width: 1200px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 70px;
    margin: 0 auto;
    padding: 0 20px;
    box-sizing: border-box;
}

.NaviBar{
    grid-column: 1 / 2;
    display: flex;
    align-items: center;
    gap : 20px;
    font-size: 16px;
    justify-self: start;
}
.Logo{
    font-size: 24px;
    color: blue;
}

.searchbar{
    grid-column: 2 / 3;
    display: flex;
    align-items: center;
    justify-self: center;
    outline: none;
}
.searchbar>button{
    background-color: #f0f0f0;
    height: 34px;
    border: 0;
    border-radius: 8px 0 0 8px;
    color: black;
    transition: color 0.1s;    
}

#searchbutton:active{
    color: #aaaaaa;
}

.searchbar>input{
    background-color: #f0f0f0;
    height: 32px;
    width: 300px;
    border: 0;
    border-radius: 0 8px 8px 0;
    outline: none;
}

.searchbar>input::placeholder{
    color: #b5b5b5;
    font-size: 14px;
    font-weight: 400;;
}

.personalarea{
    grid-column: 3 / 4;
    display: flex;
    align-items: center;
    gap : 15px;
    font-size: 16px;
    justify-self: end;
}
.loginbutton{
    background-color: black;
    color: white;
    width: 74px;
    height: 36px;
    border-radius: 8px;
    box-shadow: 0 4px 0 #333;
    transition: 0.1s ease;
}
.loginbutton:active{
    box-shadow: 0 0 0 #333;     
    transform: translateY(4px);     
    background-color: #333333;
}
.overlay{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content{
    max-width: 450px;
    width: 90%;
    position: relative;
}

.home{
    cursor: pointer;
}
.course{
    cursor: pointer;
}
.mycourse{
    cursor: pointer;
} 